@import url(https://fonts.googleapis.com/css?family=Trispace:100,200,300,regular,500,600,700,800);
@import url(https://fonts.googleapis.com/css?family=Lato:100,100italic,300,300italic,regular,italic,700,700italic,900,900italic);
@font-face {
  font-family: 'saxmono';
  src: url(/react-markdown-previewer/static/media/saxmono-webfont.8793e765.woff);
  font-style: normal;
  font-weight: bolder;
}

body {
  background-color: darkslategray;
  color: white;
}

textarea {
  width: 100%;
  height: 100vh;
  border: 1px solid white;
  background-color: inherit;
  color: #fff;
  border-radius: 5px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 18px;
  overflow-y: scroll;
  resize: none;
  padding: 7px;
}

span {
  font-family: 'saxmono';
  font-size: 17px;
  font-weight: bolder;
  /* color: green */
}

img {
  display: block;
  max-width: 80%;
  max-height: auto;
  margin: auto;
}

label {
  font-family: 'lato', sans-serif;
  font-size: 25px;
  font-weight: bold;
  padding-bottom: 8px;
}
/* 
i {
  color: green;
} */

a {
  text-decoration: underline;
  color: orange;
}

.App > p {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 17px;
  text-align: center;
}


.App > h1 {
  text-align: center;
  font-family: 'trispace', sans-serif;
  font-weight: 500;
  text-decoration: underline;
}

.container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
 }

#preview {
  width: 100%;
  height: 100vh;
  border: 1px solid white;
  border-radius: 5px;
  font-family: 'Lato', Arial, sans-serif;
  font-size: 18px;
  overflow-y: scroll;
  padding: 7px;
}

.editor-cont, .preview-cont {
  display: flex;
  flex-direction: column;
  align-content: space-around;
  height: 100%;
  width: 50%;
  text-align: left;
  margin: 20px;
}




